import { _decorator, animation, Component, Node, SkeletalAnimation, Vec3, Vec4 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Solider')
export class Solider extends Component {
    private _index:number = 0;//小兵编号
    private _team:number = 0;//小兵队伍
    private _cardInfo:any = {};//
    private _startPos:Vec3 = new Vec3();//小兵初始位置


    @property(Node)
    model: Node = null;
    @property(SkeletalAnimation)
    animation: SkeletalAnimation = null;

    start() {

    }

    update(deltaTime: number) {
        
    }
    // 初始化
    init(index:number,team:number,pos: Vec3,cardInfo){
        this.node.setPosition(pos);
        this._startPos=pos;
        this._index = index;
        this._team = team;
        this._cardInfo = cardInfo;//获取卡牌信息 

        // 小兵队伍
        if(team == 1){
            this.node.eulerAngles=new Vec3(0,180,0);
        }
        else if(team == 2){
            
        }
    }
}


